/* @import url('https://fonts.googleeapis.com/css?family=poppins:200,300,400,500,600,700,800,900&display=swap'); */

/* universal */
* {
  box-sizing: border-box;
}

/* brand color palette */
:root {
  --primary: rgb(78, 78, 78);
  --secondary: rgb(22, 80, 80);
  --tertiary: darkgray;
  --dark: black;
  --light: white;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  color: var(--primary);
  background-color: var(--light);
  line-height: 1.5;
}

h1,
h2,
h3,
h4 {
  margin: 0;
}

p {
  margin: 0;
}

header {
  padding: 15px 6% 15px 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  background-color: var(--tertiary);
  box-shadow: 0 5px 35px rgba(146, 204, 206, 0.4);
}

header h1 {
  color: var(--tertiary);
  background-color: var(--secondary);
  padding: 5px 1% 5px 6%;
  margin: 0;
  font-size: 4vw;
  line-height: 1;
}

nav {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

nav a {
  text-decoration: none;
  padding: 5px 10px;
  color: var(--secondary);
  margin-right: 10px;
  background-image: linear-gradient(
    270deg,
    var(--secondary) 0%,
    var(--primary) 100%
  );
  background-repeat: no-repeat;
  background-size: 100% 0.2em;
  background-position: bottom;
  transition: all 0.25s ease-in;
  font-size: 1.4rem;
}

nav a:hover {
  background-size: 100% 100%;
  color: var(--primary);
}

nav a:last-child {
  margin-right: 0;
}

main {
  margin-top: 20px;

  margin-bottom: 45px;
}

.banner {
  background-color: #445878;
  background-image: url("./images/mtn.jpg");
  /* linear-gradient(180deg, var(--secondary) 0%, var(--primary), 100%); */
  background-size: cover;
  background-position: center;
  background-blend-mode: soft-light;
  min-height: 30vh;
  color: var(--light);
  padding: 2% 6%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  text-align: right;
}

.banner div {
  flex: 0 0 90%;
}

.banner h2 {
  background-color: var(--secondary);
  margin: 0;
  color: var(--tertiary);
  font-size: 2.3rem;
  padding: 5px 10px;
  line-height: 1.2;
  display: inline;
}

.pg-wrapper {
  max-width: 88%;
  margin-left: auto;
  margin-right: auto;
}

.page-section {
  margin: 25px 0;
  padding: 10px 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: stretch;
}
.page-section h2 {
  flex: 0 0 20%;
  text-align: right;
  padding-right: 15px;
  border-right: 4px solid var(--primary);
  font-size: 4vw;
  line-height: 1.1;
}

.page-section > div {
  flex: 0 0 77%;
}

.page-section p {
  margin-bottom: 20px;
}

.work-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.flex-item {
  border: 5px solid var(--secondary);
  background-color: var(--primary);
  color: var(--tertiary);
  min-height: 150px;
  max-height: 150px;
  flex-basis: calc(50% - 1em);
  display: flex;
  align-items: flex-end;
  padding: 0 0 20px 0;
  margin: 0.5em;
  text-decoration: none;
  background-blend-mode: soft-light;
  background-size: 150%;
  transition: all 0.5s;
  font-size: 0.9rem;
}

.flex-item:first-child {
  background-image: url("./images/earth.jpg");
  min-height: 400px;
  flex-basis: 100%;
  height: 100%;
  width: 100%;
}

.flex-item:hover {
  background-color: var(--dark);
}

.flex-item div {
  background-color: var(--secondary);
  padding: 8px 10px;
}

.title {
  font-size: 1.6rem;
}
.flex-item h3 {
  font-size: 1.6rem;
}

.contact address {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  height: 100%;
}
.contact address a {
  color: var(--tertiary);
  padding: 5px;
  text-decoration: none;
  font-size: 1.3rem;
  font-style: normal;
  background-image: linear-gradient(
    180deg,
    var(--secondary) 0%,
    var(--primary),
    100%
  );
  background-repeat: no-repeat;
  background-size: 100% 0.2em;
  background-position: bottom;
  transition: all 0.25s ease-in;
  margin: 0 0.2rem 0.1rem;
  display: inline-block;
}

.contact address a:hover {
  background-size: 100% 88%;
  color: var(--secondary);
}

/* images loaded to html*/
.surf {
  background-image: url("./images/mtn.jpg");
}
.led {
  background-image: url("./images/earth.jpg");
}
.calc {
  background-image: url("./images/earth.jpg");
}
.puzzles {
  background-image: url("./images/earth.jpg");
}
.run {
  background-image: url("./images/earth.jpg");
}

/*media queries*/
/* Cards are split into two columns at 992px or smaller */
@media screen and (max-width: 992px) {
  /* Change the header color */
  header {
    background: #772014;
  }
}

/* Cards are stacked on top of each other at 768px or smaller */
@media screen and (max-width: 768px) {
  /* Change the header color */
  header {
    background: #8ac4ff;
  }
  nav a {
    font-size: 0.75rem;
  }
  .banner h2 {
    font-size: 20px;
    margin-bottom: 40px;
  }
  .flex-item div {
    font-size: 0.75rem;
  }
}
